export default {
	data() {
		return {
			list: [{
					name: '北半球',
					checked: false,
					disabled: false
				},
				{
					name: '南半球',
					checked: false,
					disabled: false
				},
			],
			post_trends_form: {
				title: "",
				content: "",
				post_pic: "",
			},
			action: "http://47.240.8.112/api/v1/private/upload/",
			list1: [{
					name: '男',
					checked: false,
					disabled: false
				},
				{
					name: '女',
					checked: false,
					disabled: false
				},
			],
			value: '',
			// 请求头部(上传照片)
			head: {},
			// 头像
			headimg: '',
			// 名字
			kickname: "",
			// 性别
			gender: "",
			// 岛名
			island: '',
			// 性别
			sex: '',
			// 半球
			halfball: '',
			friends: '',
			personsignature: '',
			hemisphere: '',
			myid: '6',
			current: 0,
			personinfo: ["星辰岛", "北半球", "你给的爱太假", "5-s1-9720479", "我就是我,颜色不一样的烟火", "女"],
			settinginfo: ["12", '23', '45'],
			portraitsrc: "https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3023235837,3703646437&fm=26&gp=0.jpg",
		};
	},
	methods: {
		//图片上传成功事件，lists为全部图片的数组集合
		onUploaded(lists) {
			let tmp_post_pic = ""
			const filesArr = lists;
			const reg = /http:\/\/47\.240\.8\.112\/media\//;
			for (let i = 0, len = lists.length; i < len; i++) {
				let array = JSON.parse(filesArr[i].response)
				let str = array.file
				str = str.replace(reg, "")
				if (i === lists.length - 1) {
					tmp_post_pic += str
					break;
				}
				tmp_post_pic += str + ";"
			}
			//将临时图片url赋予表单的pic
			this.headimg = tmp_post_pic
			console.log(tmp_post_pic);
		},
		radioChange(e) {
			// console.log(e);
		},
		checknum() {
			onkeyup = "this.value=this.value.replace(/\D/g,'')";
			onafterpaste = "this.value=this.value.replace(/\D/g,'')";
		},
		// 选中任一radio时，由radio-group触发
		radioGroupChange(e) {
			// console.log(e);
		},
		uploadChange(res) {
			// console.log(res)
		},
		// 获取user-info
		async getUserInfo(){
			const jwt = uni.getStorageSync("skey");
			const head = {
				'Authorization': "Bearer " + jwt
			};
			const result = await this.$myRequest({
				method: 'GET',
				url: '/users/' + this.myid + '/',
				header: head,
			})
			// console.log(result);
			this.friends = result.data.friend_sw_number
			this.island = result.data.island
			this.kickname = result.data.nickname
			if(result.data.gender === "0"){
				this.$set(this.list1[0],'checked',true)
				// this.list1[0].checked = true 
			}else{
				// this.list1[1].checked = true
				this.$set(this.list1[1],'checked',true)
			}
			
			if(result.data.hemisphere === "0"){
				// this.list[0].checked = true
				this.$set(this.list[0],'checked',true)
			}else{
				this.$set(this.list[1],'checked',true)
				// this.list[1].checked = true
			}
		},
		//提交表单
		async updatehz() {
			if (this.gender == '男') {
				this.sex = '0'
			} else {
				this.sex = '1';
			}
			if (this.hemisphere == '北半球') {
				this.halfball = '0';
			} else {
				this.halfball = '1';
			}
			const jwt = uni.getStorageSync("skey");
			const head = {
				'Authorization': "Bearer " + jwt
			};
			const result = await this.$myRequest({
				method: 'PUT',
				url: '/users/' + this.myid + '/',
				header: head,
				data: {
					nickname: this.kickname,
					gender: this.sex,
					island: this.island,
					friend_sw_number: this.friends,
					hemisphere: this.halfball,
					profile_pic: this.headimg
				},
			})
			this.kickname = ""
			this.gender = ""
			this.island = ""
			this.friends = ""
			this.$refs.uUpload.clear()
			this.hemisphere = ""
			uni.showToast({
				title:"修改信息成功",
				icon:"success"
			})
			setTimeout(()=>{
				uni.navigateBack({
					delta:1
				})
			},1000)
			// console.log(this.headimg)
			// console.log(result)
			// this.villagers = result.data.results;
			// console.log("村民"+this.villagers[1].id)
		}
	},
	onLoad() {
		const jwt = uni.getStorageSync("skey");
		const headers = {
			'Authorization': "Bearer " + jwt
		};
		this.head = headers;
	},
	onShow() {
		this.myid = uni.getStorageSync("sid")
		setTimeout(()=>{
			this.getUserInfo()
		},1000)
		// uni.startPullDownRefresh()
	}
}
